"use client";

"use client";

import { useEffect, useMemo, useState } from "react";
import dynamic from "next/dynamic";
import { Navbar } from "@/components/ui/Navbar";
import { fetchSegments, searchPredictions } from "@/lib/api";
import Link from "next/link";
import { Loader2 } from "lucide-react";

// Dynamically import Leaflet map to avoid SSR issues
const LeafletMap = dynamic(() => import("@/components/dashboard/LeafletMap"), {
    ssr: false,
    loading: () => <div className="w-full h-full min-h-[500px] animate-pulse bg-muted rounded-xl" />
});

export default function MapPage() {
    const [segments, setSegments] = useState([]);
    const [readings, setReadings] = useState([]); // raw readings
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState("");
    const [query, setQuery] = useState("");

    useEffect(() => {
        let mounted = true;
        const load = async () => {
            setLoading(true);
            try {
                const [segData, readingData] = await Promise.all([
                    fetchSegments(),
                    searchPredictions({ limit: 1000 })
                ]);
                if (!mounted) return;
                setSegments(segData || []);
                setReadings(readingData || []);
                setError("");
            } catch (err) {
                if (mounted) setError(err.message || "Failed to load map data");
            } finally {
                if (mounted) setLoading(false);
            }
        };
        load();
        const id = setInterval(load, 30000);
        return () => {
            mounted = false;
            clearInterval(id);
        };
    }, []);

    const indexedSegments = useMemo(() => {
        const m = new Map();
        segments.forEach((s) => m.set(s.road_id, s));
        return m;
    }, [segments]);

    // Keep only the most recent reading per road_id (current snapshot)
    const latestReadings = useMemo(() => {
        const latest = new Map();
        readings.forEach((r) => {
            const existing = latest.get(r.road_id);
            const ts = r.timestamp ? new Date(r.timestamp).getTime() : 0;
            const exTs = existing?.timestamp ? new Date(existing.timestamp).getTime() : -Infinity;
            if (!existing || ts > exTs) {
                latest.set(r.road_id, r);
            }
        });
        return Array.from(latest.values());
    }, [readings]);

    const filtered = useMemo(() => {
        if (!query) return latestReadings;
        const q = query.toLowerCase();
        return latestReadings.filter((r) => {
            const seg = indexedSegments.get(r.road_id);
            const hay = `${r.road_id} ${seg?.road_name || ""} ${seg?.segment_name || ""}`.toLowerCase();
            return hay.includes(q);
        });
    }, [latestReadings, query, indexedSegments]);

    const markers = useMemo(() => {
        return filtered
            .map((r, idx) => {
                const seg = indexedSegments.get(r.road_id);
                if (seg?.lat == null || seg?.lon == null) return null;
                const pct = r.congestion_pct || 0;
                const color = pct >= 80 ? "#ef4444" : pct >= 60 ? "#f59e0b" : "#10b981";
                return {
                    id: `${r.road_id}-${idx}`,
                    lat: seg.lat,
                    lng: seg.lon,
                    popupContent: (
                        <div className="p-2 space-y-1">
                            <p className="font-bold">{seg.segment_name || seg.road_name}</p>
                            <p className="text-sm text-muted-foreground">{r.road_id}</p>
                            <p className="text-sm">Congestion: {pct}% {r.congestion_band ? `(${r.congestion_band})` : ""}</p>
                            <Link href={`/analytics?road_id=${r.road_id}`} className="text-primary text-sm font-semibold">View forecast →</Link>
                        </div>
                    ),
                };
            })
            .filter(Boolean);
    }, [filtered, indexedSegments]);

    const heatpoints = useMemo(() => {
        return filtered
            .map((r, idx) => {
                const seg = indexedSegments.get(r.road_id);
                if (seg?.lat == null || seg?.lon == null) return null;
                const pct = r.congestion_pct || 0;
                const color = pct >= 80 ? "#ef4444" : pct >= 60 ? "#f59e0b" : "#10b981";
                return { id: `${r.road_id}-heat-${idx}`, lat: seg.lat, lng: seg.lon, intensity: pct, color };
            })
            .filter(Boolean);
    }, [filtered, indexedSegments]);

    return (
        <main className="min-h-screen bg-background text-foreground">
            <Navbar />
            <div className="pt-20 px-4 max-w-7xl mx-auto space-y-6 h-[calc(100vh-80px)]">
                <div className="flex flex-col h-full gap-4">
                    <div className="flex items-center justify-between flex-wrap gap-3">
                        <h1 className="text-2xl font-bold">Live Traffic Grid</h1>
                        <div className="flex items-center gap-2 text-sm text-emerald-500">
                            <span className="relative flex h-2 w-2">
                                <span className="animate-ping absolute inline-flex h-full w-full rounded-full bg-emerald-400 opacity-75"></span>
                                <span className="relative inline-flex rounded-full h-2 w-2 bg-emerald-500"></span>
                            </span>
                            System Online
                        </div>
                    </div>

                    <div className="grid grid-cols-1 lg:grid-cols-3 gap-6 h-full pb-6">
                        {/* Real Map Container */}
                        <div className="lg:col-span-2 h-full min-h-[500px] rounded-xl overflow-hidden border border-border relative">
                            <LeafletMap markers={markers} heatpoints={heatpoints} />
                            {loading && (
                                <div className="absolute inset-0 flex items-center justify-center bg-background/60 backdrop-blur-sm">
                                    <Loader2 className="h-6 w-6 animate-spin text-muted-foreground" />
                                </div>
                            )}
                        </div>

                        {/* Live feed and search */}
                        <div className="lg:col-span-1 h-full min-h-[500px] flex flex-col gap-4">
                            <div className="p-4 rounded-xl border border-border bg-card/50 backdrop-blur-sm space-y-3">
                                <p className="text-sm font-semibold">Search location</p>
                                <input
                                    className="w-full p-2.5 rounded-md bg-background border border-border/60 text-sm focus:outline-none focus:ring-2 focus:ring-primary/50"
                                    placeholder="Search by road, segment, or ID"
                                    value={query}
                                    onChange={(e) => setQuery(e.target.value)}
                                />
                                {error && <p className="text-sm text-red-500">{error}</p>}
                                <p className="text-xs text-muted-foreground">Showing {filtered.length} of {readings.length} live readings</p>
                            </div>

                            <div className="flex-1 overflow-auto rounded-xl border border-border bg-card/40 backdrop-blur-sm p-3 space-y-3">
                                <p className="text-sm font-semibold">Live traffic feed</p>
                                {loading && <p className="text-sm text-muted-foreground">Loading…</p>}
                                {!loading && filtered.length === 0 && <p className="text-sm text-muted-foreground">No locations match your search.</p>}
                                {filtered.map((r, idx) => {
                                    const seg = indexedSegments.get(r.road_id) || {};
                                    const pct = r.congestion_pct || 0;
                                    const color = pct >= 80 ? "text-red-500" : pct >= 60 ? "text-orange-500" : "text-emerald-500";
                                    return (
                                        <div key={`${r.road_id}-${idx}`} className="p-3 rounded-lg border border-border/60 bg-background/40">
                                            <div className="flex items-center justify-between gap-2">
                                                <div>
                                                    <p className="font-semibold">{seg.segment_name || seg.road_name || r.road_id}</p>
                                                    <p className="text-xs text-muted-foreground">{seg.road_name} · {r.road_id}</p>
                                                </div>
                                                <span className={`text-sm font-semibold ${color}`}>{Math.round(pct)}%</span>
                                            </div>
                                            {r.congestion_band && <p className="text-xs text-muted-foreground mt-1">Band: {r.congestion_band}</p>}
                                            <div className="flex items-center justify-between mt-2">
                                                <span className="text-xs text-muted-foreground">{r.timestamp?.replace("T", " ")}</span>
                                                <Link href={`/analytics?road_id=${r.road_id}`} className="text-primary text-xs font-semibold">View forecast</Link>
                                            </div>
                                        </div>
                                    );
                                })}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </main>
    );
}
